<template>
  <div class="mesForm">
    <el-dialog
      title=""
      :visible.sync="taskFormVisible"
      :before-close="hanldeClose"
      :modal-append-to-body="false"
      width="500px"
      top="0"
    >
      <el-form :model="taskForm" status-icon :rules="rules" ref="taskForm" label-width="100px">
        <el-form-item label="" prop="start_time">
          <el-date-picker v-model="taskForm.start_time" type="datetime" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="end_time">
          <el-date-picker v-model="taskForm.end_time" type="datetime" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="grade">
          <el-select v-model="taskForm.grade" placeholder="">
            <el-option label="2016" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="major">
          <el-select v-model="taskForm.major" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.stop="submitForm('taskForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ["taskFormVisible", "taskForm"],
  data() {
    return {
      rules: {
        start_time: [
          { required: true, message: "", trigger: "blur" }
        ],
        end_time: [
          { required: true, message: "", trigger: "blur" }
        ],
        grade: [{ required: true, message: "", trigger: "blur" }],
        major: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    hanldeClose() {
      this.$emit("show", false);
      this.$refs.taskForm.resetFields();
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (!valid) return;
        console.log(valid);
        console.log(this.addForm);
      });
    }
  }
};
</script>
